import { useState, useEffect } from "react";

const useFetch = (url) => {
	const [state, setState] = useState({
		data: null,
		loading: true,
		error: null,
	});

	useEffect(() => {
		setState({
			data: null,
			loading: true,
			error: null,
		});

		fetch(url)
			.then((response) => response.json())
			.then((data) => {
				setState({
					loading: false,
					error: null,
					data: data.message,
				});
			});
	}, [url]);

	return state;
};

export default useFetch;
